<template>
    <div class="vscroller">
        <div v-if="scrollerTitle" class="vscroller__head">{{scrollerTitle}}</div>
        <div class="vscroller__body">
            <ul :style="{ webkitTransition: '-webkit-transform ' + transitionDuration / 1000 + 's ease-out', transition: 'transform ' + transitionDuration / 1000 + 's ease-out', webkitTransform: 'translate3d(0px, ' + currentTranslatedY + 'px, 0px)', transform: 'translate3d(0px, ' + currentTranslatedY + 'px, 0px)' }" ref="ul">
                <template v-for="item in itemList" :data-val="item.value" :class="item.selected ? 'vselected' : ''">
                    <div class="vscroller_item" :key="item.value">
                        <div class="avatar-day">
                            <p>
                                {{item.day_of_week}}
                            </p>
                            <p>
                                {{item.day}}
                            </p>
                        </div>
                        <div style="display: flex; flex-direction: column; justify-content: center">
                            <div v-for="p in item.pros" :key="p.color" class="promotion-detail">
                                <div class="dot-promotion" :style="{backgroundColor: p.color}"></div>
                                <span >{{p.name}} | {{p.time}}</span>
                            </div>
                        </div>
                        <div class="arr-day">
                            <i @click="$emit('show_popup', 'new_year', item.pros)" class="el-icon-arrow-right"></i>
                        </div>
                    </div>
                </template>
            </ul>
        </div>
    </div>
</template>

<script>
    // import Avatar from 'vue-avatar';

    export default {
        name: 'Scroller',
        components: {
            // Avatar
        },
        props: {
            scrollerTitle: String,
            itemList: Array,
            show_popup: Function,
        },
        data() {
            return {
                selectedIndex: 0,
                selectedValue: 1,
                startPosY: 0,
                currentPosY: 0,
                startTime: 0,
                endTime: 0,
                lastTime: (new Date()).getTime(),
                transitionDuration: 0,
                lastPosY: 0,
                lastV: 0,
                startTranslatedY: 80,
                currentTranslatedY: 80,
                haveClicked: false,
                isMouseDown: false,
                totalHeight: 120,
                scroll: true,
            };
        },
        created() {
        },
        watch: {
            itemList: {
                deep: true,
                handler: function()  {
                    if (!this.scroll) this.initData();
                    this.scroll = false;
                }
            }
        },
        mounted() {
            let supportedTouch = false;
            this.initData();
            if ('ontouchstart' in window) {
                supportedTouch = true;
            }
            if (supportedTouch) {
                this.bindTouchEvents();
            } else {
                this.bindMouseEvents();
            }
            this.bindClickEvent();
        },

        methods: {
            initData() {
                if (this.itemList.length > 0) {
                    this.itemList.forEach((item, index) => {
                        if (item.selected === true) {
                            this.selectedValue = item.value;
                            // if (index === 0 ) index = index +1;
                            this.selectedIndex = index;
                            return false;
                        }
                        return true;
                    });
                    if (this.selectedIndex === this.itemList.length - 1){
                        this.startTranslatedY = - (this.selectedIndex * 120);        //106
                    }else{
                        this.startTranslatedY = 0 - (this.selectedIndex * 120);     //old 0 & 40
                    }
                    this.currentTranslatedY = this.startTranslatedY;
                } else {
                    this.itemList = [{ value: 1, name: 'Please select..', selected: true }];
                }
                this.totalHeight = this.itemList.length * 120;
                this.scroll = false;
            },
            bindTouchEvents() {
                const el = this.$refs.ul;
                // bind events
                el.addEventListener('touchstart', (e) => {
                    this.startPosY = e.changedTouches[0].pageY;
                    this.currentPosY = this.startPosY;
                    this.startTime = (new Date()).getTime();
                    this.startTranslatedY = this.currentTranslatedY;
                    this.lastV = 0;
                }, false);
                el.addEventListener('touchmove', (e) => {
                    e.preventDefault(); // prevent default scrolling event when touch moving
                    this.lastV = (e.changedTouches[0].pageY - this.lastPosY)
                        / ((new Date()).getTime() - this.lastTime);
                    this.currentPosY = e.changedTouches[0].pageY;
                    this.currentTranslatedY = (this.startTranslatedY + this.currentPosY) - this.startPosY;
                    this.lastPosY = this.currentPosY;
                    this.lastTime = (new Date()).getTime();
                }, false);
                el.addEventListener('touchend', () => {
                    this.endTime = (new Date()).getTime();
                    if (Math.abs(this.currentPosY - this.startPosY) > 5 && this.endTime - this.startTime > 50) {
                        const v = this.lastV;
                        const s = v > 0 ? (0.5 * (v ** 2)) / 0.001 : (-0.5 * (v ** 2)) / 0.001;
                        const t = Math.abs(v) / 0.001;
                        let currentTranslatedY = this.currentTranslatedY;
                        currentTranslatedY += s;
                        const residue = currentTranslatedY % 120;            // old 40          -> dd 80
                        if (Math.abs(residue) >= 20) {
                            if (residue < 0) {
                                currentTranslatedY += ((120 + residue) * (-1));          // old 40   80
                            } else {
                                currentTranslatedY += (120 - residue);                   // old 40   80
                            }
                        } else {
                            currentTranslatedY -= residue;
                        }
                        if (currentTranslatedY > 10) {
                            currentTranslatedY = 80; //sdsfdf                    // old 80
                        } else if (currentTranslatedY < (this.totalHeight - 106) * -1) {      // old 120
                            currentTranslatedY = (this.totalHeight - 120) * (-1);                //old -> 120

                        }
                        let selectedIndex = Math.abs((currentTranslatedY) / (120)).toFixed(0);
                        if (selectedIndex >= this.itemList.length) selectedIndex = this.itemList.length -1
                        this.transitionDuration = t;
                        this.currentTranslatedY = currentTranslatedY;
                        setTimeout(() => {

                            // this.itemList[this.selectedIndex].selected = false;
                            this.selectedIndex = selectedIndex;
                            this.itemList[this.selectedIndex].selected = true;
                            this.selectedValue = this.itemList[this.selectedIndex].value;
                            this.haveClicked = false;

                        }, t);
                    } else {
                        this.haveClicked = true;
                    }
                    this.startPosY = 0;
                    this.currentPosY = 0;
                    this.startTranslatedY = 0;
                    this.startTime = 0;
                    this.endTime = 0;
                    this.lastPosY = 0;
                    this.lastV = 0;
                    this.scroll = true;
                }, false);
            },
            bindMouseEvents() {
                const el = this.$refs.ul;
                let mouseDown = null;
                let mouseMove = null;
                let mouseUp = null;
                let mouseLeave = null;
                let mouseWheel = null;
                mouseDown = (e) => { // mouse down event
                    this.isMouseDown = true;
                    this.startPosY = e.pageY;
                    this.currentPosY = this.startPosY;
                    this.startTime = (new Date()).getTime();
                    this.startTranslatedY = this.currentTranslatedY;
                    el.addEventListener('mousemove', mouseMove);
                    el.addEventListener('mouseup', mouseUp);
                    el.addEventListener('mouseleave', mouseLeave);
                };
                mouseMove = (e) => { // mouse move event
                    if (this.isMouseDown) {
                        e.preventDefault(); // prevent default selecting event when mouse moving
                        this.lastV = (e.pageY - this.lastPosY)
                            / ((new Date()).getTime() - this.lastTime);
                        this.currentPosY = e.pageY;
                        this.currentTranslatedY = (this.startTranslatedY + this.currentPosY) - this.startPosY;
                        this.lastPosY = this.currentPosY;
                        this.lastTime = (new Date()).getTime();
                        this.haveClicked = false;
                    }
                };
                mouseUp = () => { // mouse up event
                    this.endTime = (new Date()).getTime();
                    if (Math.abs(this.currentPosY - this.startPosY) > 5 && this.endTime - this.startTime > 20) {
                        const v = this.lastV;
                        const s = v > 0 ? (0.5 * (v ** 2)) / 0.001 : (-0.5 * (v ** 2)) / 0.001;
                        const t = Math.abs(v) / 0.001;
                        let currentTranslatedY = this.currentTranslatedY;
                        currentTranslatedY += s;
                        const residue = currentTranslatedY % 40;
                        console.log('mouse', residue)
                        if (Math.abs(residue) >= 20) {
                            if (residue < 0) {
                                currentTranslatedY += ((40 + residue) * (-1));
                            } else {
                                currentTranslatedY += (40 - residue);
                            }
                        } else {
                            currentTranslatedY -= residue;
                        }
                        if (currentTranslatedY > 80) {
                            currentTranslatedY = 80;
                        } else if (currentTranslatedY < (this.totalHeight - 120) * (-1)) {
                            currentTranslatedY = (this.totalHeight - 120) * (-1);
                        }
                        const selectedIndex = Math.abs((currentTranslatedY - 80) / (-40));
                        this.transitionDuration = t;
                        this.currentTranslatedY = currentTranslatedY;
                        setTimeout(() => {
                            this.itemList[this.selectedIndex].selected = false;
                            this.selectedIndex = selectedIndex;
                            this.itemList[this.selectedIndex].selected = true;
                            this.selectedValue = this.itemList[this.selectedIndex].value;
                            this.haveClicked = false;
                        }, t);
                    } else {
                        this.haveClicked = true;
                    }
                    this.startPosY = 0;
                    this.currentPosY = 0;
                    this.startTranslatedY = 0;
                    this.startTime = 0;
                    this.endTime = 0;
                    this.lastPosY = 0;
                    this.lastV = 0;
                    this.isMouseDown = false;
                    el.removeEventListener('mousemove', mouseMove);
                    el.removeEventListener('mouseup', mouseUp);
                    el.removeEventListener('mouseleave', mouseLeave);
                };
                mouseLeave = () => { // mouse leave event
                    if (this.isMouseDown) {
                        mouseUp();
                    }
                };
                mouseWheel = (e) => { // mouse wheel event
                    this.startTranslatedY = this.currentTranslatedY;
                    let currentTranslatedY = this.startTranslatedY + (e.deltaY * 0.5);
                    const residue = currentTranslatedY % 40;
                    if (Math.abs(residue) >= 20) {
                        if (residue < 0) {
                            currentTranslatedY += ((40 + residue) * (-1));
                        } else {
                            currentTranslatedY += (40 - residue);
                        }
                    } else {
                        currentTranslatedY -= residue;
                    }
                    if (currentTranslatedY > 80) {
                        currentTranslatedY = 80;
                    } else if (currentTranslatedY < (this.totalHeight - 120) * (-1)) {
                        currentTranslatedY = (this.totalHeight - 120) * (-1);
                    }
                    this.transitionDuration = 0.2;
                    this.currentTranslatedY = currentTranslatedY;
                    const selectedIndex = Math.abs((currentTranslatedY - 80) / (-40));
                    setTimeout(() => {
                        this.itemList[this.selectedIndex].selected = false;
                        this.selectedIndex = selectedIndex;
                        this.itemList[this.selectedIndex].selected = true;
                        this.selectedValue = this.itemList[this.selectedIndex].value;
                    }, this.transitionDuration);
                    this.startTranslatedY = 0;
                };
                // bind events
                el.addEventListener('mousedown', mouseDown);
                el.addEventListener('wheel', mouseWheel);
            },
            bindClickEvent() {
                const el = this.$refs.ul;
                console.log('Vao bind click')
                el.querySelectorAll('li').forEach(($li, index) => {
                    $li.addEventListener('click', () => {
                        if (this.haveClicked) {
                            const itemPositionY = $li.offsetTop;
                            const currentTranslatedY = 80 - itemPositionY;
                            this.transitionDuration = 0;
                            this.currentTranslatedY = currentTranslatedY;
                            this.itemList[this.selectedIndex].selected = false;
                            this.selectedIndex = index;
                            this.itemList[this.selectedIndex].selected = true;
                            this.selectedValue = this.itemList[this.selectedIndex].value;
                            this.haveClicked = false;
                        }
                    });
                    return true;
                });
            },
        },

    };
</script>

<style>
    .avatar-day{
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        width: 10vh;
        height: 10vh;
        letter-spacing: 0.3vh;
        background-color: black;
        color: white;
        font-weight: 900;
        font-size: 3vh;
        font-family: 'Montserrat', sans-serif;
    }
    .vscroller{
        font-family: arial,verdana,sans-serif;
        padding: .5em .25em;
        min-width: 40px;
        max-width: 100%;
        -webkit-box-flex: 1;
        -webkit-flex: 1 auto;
        -ms-flex: 1 auto;
        flex: 1 auto;
        -ms-touch-action: none;
        touch-action: none;
    }
    .vscroller__head {
        line-height: 30px;
        height: 30px;
        text-align: center;
        white-space: nowrap;
        font-weight: bold;
        text-transform: upperscse;
        color: #4eccc4;
        width: 100%;
        font-size: 1.375em;
    }
    .vscroller__body {
        min-height: 20vh;
        max-height: 30vh;
        overflow: hidden;
        position: relative;
        background-color: #2f2f2f;
        /*background-color: yellow;*/
    }
    .vscroller_item{
        background-color: #ecebeb;
        position: relative;
        border-radius: 25px;
        display: flex;
        flex-direction: row;
        min-height: 106px;
        padding-left: 4%;
        padding-top: 3%;
        padding-bottom: 3%;
        margin-left: 5%;
        margin-right: 5%;
        margin-top: 4%;
        margin-bottom: 2%;
        align-items: center;
    }
    .arr-day {
        margin-left: 5%;
        font-size: 3vh;
        position: absolute;
        right: 2vw;
    }
    .vscroller_item > div{
        border-radius: 15px!important;
    }
    .vscroller_item > div > span {
        font-size: 3.8vh;
        letter-spacing: 0.1vh;
        line-height: 4vh;
        color: white;
        font-family: Montserrat,sans-serif;
    }
    .promotion-detail{
        padding-left: 5px;
        padding-bottom: 5px;
        padding-top: 2px;
        display: flex;
        align-items: center;
        flex-direction: row;
    }

    /*.vscroller__body:before {*/
    /*    content: '';*/
    /*    position: absolute;*/
    /*    left: 0;*/
    /*    top: 80px;*/
    /*    bottom: auto;*/
    /*    right: auto;*/
    /*    height: 1px;*/
    /*    width: 100%;*/
    /*    background-color: #4eccc4;*/
    /*    display: block;*/
    /*    z-index: 1;*/
    /*}*/
    /*.vscroller__body:after {*/
    /*    content: '';*/
    /*    position: absolute;*/
    /*    left: 0;*/
    /*    bottom: 80px;*/
    /*    right: auto;*/
    /*    top: auto;*/
    /*    height: 1px;*/
    /*    width: 100%;*/
    /*    background-color: #4eccc4;*/
    /*    display: block;*/
    /*    z-index: 1;*/
    /*}*/
    .vscroller__body ul {
        display: block;
        margin: 0;
        padding: 0;
        position: relative;
        z-index: 3;
        transform: translate3d(0px, 0px, 0px);
    }
    .vscroller__body li {
        display: block;
        height: 40px;
        line-height: 40px;
        cursor: pointer;
        text-align: center;
        padding: 0 5px;
        margin: 0;
        white-space: nowrap;
        vertical-align: bottom;
        width: 100%;
        color: #454545;
        position: relative;
        overflow: hidden;
        text-overflow: ellipsis;
    }
    .vscroller__body li.vselected {
        font-weight: 700;
    }
    .promotion-detail > span {
        font-size: 2.5vw!important;
        padding-left: 1vw;
        /*line-height: 2vh;*/
    }
    .promotion-detail .dot-promotion {
        width: 8px;
        height: 8px;
        margin-left: 2px;
        border-radius: 50%;
        display: inline-block;
    }
</style>