<template>
    <div id="main">
        <div class="top-calendar">
            <el-col :span="9">
                <el-row class="title-calendar">
                    <span>{{year}}</span>
                    <span>{{month_name}}</span>
                </el-row>
                <el-row>
                    <table style="margin-left: 1.3vw;">
                        <tr class="list-promo" v-for="p in list_promotion" :key="p.color">
                            <td :class="show_btn_detail === p.slug ? 'btn-pros-active' : ''">
                                <div class="dot-promotion"
                                     :style="{backgroundColor: p.color}">
                                </div>
                                <span @click="filterPromotion(p.slug)">{{p.name}}</span>
                            </td>
                            <td v-if="show_btn_detail === p.slug"  @click="show_popup(p.slug)">
                                <i class="el-icon-caret-right"></i>
                                <span v-if="p.slug !== 'all'">DETAILS</span>
                            </td>
                        </tr>
                    </table>

                </el-row>
            </el-col>
            <el-col style="padding-top: 6px" :span="15">
                <el-row>
                    <el-calendar>
                        <!-- Use 2.5 slot syntax. If you use Vue 2.6, please use new slot syntax-->
                        <template
                                slot="dateCell"
                                slot-scope="{data}" >
                            <div style="width: 60%; margin-left: 15%" >
                                <p :class="check_day(data)"
                                    :style="!show_all && promotions[0].day.includes(parseInt(data.day.split('-').slice(2).join('-'))) ? {borderColor: promotions[0].color} : ''"
                                   @click="select_day(data)"
                                >
                                    {{ parseInt(data.day.split('-').slice(2).join('-')) }}
                                </p>
                                <div :class="more_5_pros.includes(parseInt(data.day.split('-').slice(2))) ? 'dot-container-width-70' :  'dot-container'">
                                    <template v-for="p in promotions" >
                                            <span :key="p.color" v-if="check_current_day_in_month(p.day, data) && show_all"  class="dot-promotion"
                                                  :style="{backgroundColor: p.color}">
                                            </span>
<!--                                            <br :key="p.color" v-if=""/>-->
                                    </template>
                                </div>
                            </div>
                        </template>
                    </el-calendar>
                </el-row>
                <el-row class="calendar-note">
                    <el-col class="note-number" :span="6">
                        <span>{{title.hour}}</span>
                        <span>{{title.minus}}</span>
                    </el-col>
                    <el-col class="note-text" :span="17">
                        <span>{{title.text}}</span>
                    </el-col>
                </el-row>
            </el-col>
        </div>
        <div class="list_day">

            <scroller @show_popup="show_popup" :itemList="day_promotions"></scroller>

        </div>
        <div class="cfooter">
            <el-row class="info-jp">
                <table>
                    <tr>
                        <td>FLOOR WIDE P1</td>
                        <td style="font-weight: 600;">: ${{wide_p1}}</td>
                        <td>COMMUNITY ITALY</td>
                        <td style="font-weight: 600;">: ${{italy}}</td>
                    </tr>
                    <tr>
                        <td>MIGHTY CASH</td>
                        <td style="font-weight: 600;">: ${{might_cash}}</td>
                        <td>GOOD FORTUNE</td>
                        <td style="font-weight: 600;">: ${{good_fortune}}</td>
                    </tr>
                </table>
            </el-row>
            <el-row class="cbnt">
                <div>
                    <span :style="lang === 'eng' ? {color: '#c2b1ad'} : {}" @click="change_language('eng')"><u>ENGLISH</u></span>
                </div>
                <div >
                    <span :style="lang === 'cn' ? {color: '#c2b1ad'} : {}" @click="change_language('cn')"><u>中國語</u></span>
                </div>
                <div >
                    <span :style="lang === 'kor' ? {color: '#c2b1ad'} : {}" @click="change_language('kor')"><u>한국어</u></span>
                </div>
            </el-row>
            <el-row class="auth">
                <img class="img-logo" src="../assets/logo.png"/>
                <span>PALAZZO CLUB @ SHERATON HOTEL & TOWERS</span>
            </el-row>
        </div>


<!--        Popup-->
        <el-dialog
                :visible.sync="dialogVisible"
                width="100%"
                top="9%"
                :append-to-body="true"
        >
            <div class="popup-image">
                <img class="img-promotion" v-if="url_img === 'zpay'" src="../assets/Zpay.jpg"/>
                <img class="img-promotion" v-if="url_img === 'zpay_day'" src="../assets/ZpayDay.jpg"/>
                <img class="img-promotion" v-if="url_img === 'zpay_add_on'" src="../assets/ZpayAddOn.jpg"/>
                <img class="img-promotion" v-if="url_img === 'mb_draw'" src="../assets/MemberDraw.jpg"/>
                <img class="img-promotion" v-if="url_img === 'mb_night'" src="../assets/MemberNight.jpg"/>
                <img class="img-promotion" v-if="url_img === 'level_rush'" src="../assets/LevelRush.jpg"/>
                <img class="img-promotion" v-if="url_img === 'level_rush_day'" src="../assets/LevelRushDay.jpg"/>
                <img class="img-promotion" v-if="url_img === 'hot_shot'" src="../assets/HotShot.jpg"/>
                <img class="img-promotion" v-if="url_img === 'muay_thai'" src="../assets/MuayThai.jpg"/>
                <img class="img-promotion" v-if="url_img === 'top_10_rl'" src="../assets/Top10RL.jpg"/>
                <img class="img-promotion" v-if="url_img === 'festival'" src="../assets/FestivalPromo.jpg"/>
                <img class="img-promotion" v-if="url_img === 'cj'" src="../assets/CommunityJP.jpg"/>
                <div style="width: 100%; height: 100%; background-color: #2f2f2f;"  v-if="url_img === 'new_year'" ></div>
<!--                promotions info popup-->

                <div v-if="url_img === 'new_year'" class="popup-image-info">
                    <div v-for="(p) in info_promotions" :key="p.slug" class="container-info">
                        <p class="title"><u>{{p.title}}</u></p>
                        <div  v-html="p.info"></div>
                    </div>

                    <div class="tern">
                        <p>{{data.term}}</p>
                    </div>
                </div>
                <div v-if="url_img === 'new_year' && show_btn_prev" class="btn-arr-pre">
                    <i @click="prev_page(index_page)" class="el-icon-arrow-left"></i>
                </div>
                <div v-if="url_img === 'new_year' && show_btn_next" class="btn-arr-next">
                    <i @click="next_page(index_page)" class="el-icon-arrow-right"></i>
                </div>
            </div>
        </el-dialog>
    </div>
</template>


<script>
    // import cn_data from '../cn_data';
    // import kor_data from '../kor_data';
    import moment from 'moment';
    import api from '../api';


    import Scroller from "@/components/Scroller";
    import _ from 'lodash';
    const API_URL = 'http://127.0.0.1:8000/api';


    export default {
        name: 'Main',
        components: {
            Scroller
        },
        data() {
            return {
                msg: 'Welcome to Your Vue.js App',
                list_promotion: '',
                promotions: '',
                isCollapse: false,
                count: 0,
                show_all: true,
                day_promotions: '',
                dialogVisible: false,
                url_img: '',
                show_btn_detail: '',
                promotion_check_count_dot: '',
                title: {},
                show_btn_next: false,
                show_btn_prev: false,
                index_page: 1,
                info_promotions: [],
                promotion_bk: [],
                data: '',
                year: '',
                month_name: '',
                more_5_pros: [],
                wide_p1: '',
                italy: '',
                might_cash: '',
                good_fortune: '',
                lang: 'eng',
                endata:{},
            }
        },
        beforeCreate(){
        api.post(`${API_URL}/gettitle`)
            .then(res => {
            this.datatitle = res.data.title
            const time = res.data.title.time
            const title = res.data.title.promo_entitle
            Object.assign(this.title, {text: title.toUpperCase(), hour: time.substring(0,2), minus: time.substring(3)})
        });
            this.change_language('eng');

        },
        created () {
            this.get_engdata();
            this.init();
            this.show_title();

            this.promotion_day();
            this.filterPromotion('ALL');
            this.get_jackpot_value();
        },
        mounted(){
            this.change_language('eng');
        },
        methods: {

            init() {
                const d = new Date();
                // get year
                this.year = d.getFullYear();

                // get month name
                this.month_name = moment().format('MMMM').toUpperCase();
            },

            get_jackpot_value () {
                api.post(`${API_URL}/jackpot`)
                    .then(res => {
                        this.wide_p1 = res.data.jackpot[0].j_value
                        this.italy = res.data.jackpot[1].j_value
                        this.might_cash = res.data.jackpot[2].j_value
                        this.good_fortune = res.data.jackpot[3].j_value
                    })
            },
            get_engdata () {
                api.post(`${API_URL}/webengdata`).then(res => {

                        this.list_promotion = res.data.engdata.list_promotion
                        this.promotions = res.data.engdata.promotions
                        this.info_promotions = res.data.engdata.info_promotions
                        this.term = res.data.engdata.term
                        this.titles = res.data.engdata.titles
                    })
                    
            },
            numberWithCommas(x) {
                return String(x).replace(/(.)(?=(\d{3})+$)/g,'$1,');
            },

            show_title() {
                api.post(`${API_URL}/gettitle`)
                    .then(res => {
                    this.datatitle = res.data.title
                    const time = res.data.title.time
                    const title = res.data.title.promo_entitle
                    Object.assign(this.title, {text: title.toUpperCase(), hour: time.substring(0,2), minus: time.substring(3)})
                })
            },
            change_language (lang) {
                // this.data = lang === 'cn' ? cn_data : kor_data;
                this.lang = lang;

                const time = this.datatitle.time
                const title = lang==='eng'? this.datatitle.promo_entitle :lang==='cn'? this.datatitle.promo_cntitle  :this.datatitle.promo_krtitle 
                Object.assign(this.title, {text: title.toUpperCase(), hour: time.substring(0,2), minus: time.substring(3)})
                this.promotion_day();
                this.filterPromotion('ALL');
            // this.created();
            },


            filterPromotion (name) {
                 api.post(`${API_URL}/webengdata`).then(res => {
                if (name === "ALL" | name === "all"){
                    this.promotions = res.data.engdata.promotions;
                    this.show_all = true;
                }
                else{
                    this.show_all = false;
                    this.promotions = res.data.engdata.promotions.filter(p => {
                        return p.slug === name
                    })
                }
                 })
              this.show_btn_detail = name;

            },
            check_day(data){
                const day = parseInt(data.day.split('-').slice(2));
                const month = parseInt(data.day.split('-').slice(1))
                const d = new Date()
                const curr_month = d.getMonth() + 1;
                let _class = '';
                if (month === curr_month) {
                    _class = _class + ' title-day '
                    if (!this.show_all){
                        if(this.promotions[0].day.includes(day)){
                            _class = _class + ' title-day-active '
                        }
                    }
                    if(day === d.getDate()){
                        _class = _class + ' current-day '
                    }
                }else {
                    _class = _class + ' day-not-current-month '
                }
                return _class
            },

            disable_day_not_in_month (data){
                // const day = parseInt(eng_data.day.split('-').slice(2));
                const month = parseInt(data.day.split('-').slice(1))
                const d = new Date()
                const curr_month = d.getMonth() + 1;
                if (month !== curr_month) {
                    return true
                }
            },

            check_current_day_in_month (days=[],date){
                // date -> yyyy-mm-dd
                const day_of_calendar = parseInt(date.day.split('-').slice(2).join('-'))
                const month_of_calendar = parseInt(date.day.split('-').slice(1).join('-'))
                const d = new Date();
                const month = d.getMonth()
                if (days.includes(day_of_calendar) && month === month_of_calendar -1) return true
                return false
            },
            promotion_day () {
                 api.post(`${API_URL}/webengdata`).then(res => {
                     let pdays = [];
                const d = new Date();
                const month_now = d.getMonth();
                const year_now = d.getFullYear();
                this.daysOfWeek(year_now, month_now, 31);
                const days_in_month = this.daysInMonth(d.getMonth()+1);
                for(let i = 1; i <= days_in_month; i++){
                    const _pros = res.data.engdata.promotions.reduce((_p, p) => {
                        if(p.day.includes(i)){

                            // if( p.name === 'MEMBERS DRAW'){
                            //     _p.push({name: 'M.DRAW', time: p.time, color: p.color, slug: p.slug})
                            // }else{
                                if (p.slug === 'level_rush' && this.daysOfWeek(year_now, month_now, i) === 'SAT'){
                                    _p.push({name: p.name, time: '11.00 pm', color: p.color, slug: p.slug})
                                }else{
                                    _p.push({name: p.name, time: this.lang === 'eng'? p.endetail :this.lang === 'kor'? p.krdetail: p.cndetail, color: p.color, slug: p.slug})
                                }

                            // }

                        }
                        return _p
                    }, [])
                    pdays.push({
                        day: i < 10 ? `0${i}` : i,
                        day_of_week: this.daysOfWeek(year_now, month_now, i),
                        selected: d.getDate() === i,
                        pros: _pros,
                    })
                }
                console.log('pday', pdays)
                this.more_5_pros = pdays.map(d => {
                    if(d.pros.length > 3) return d.day
                })
                this.day_promotions = pdays;
                 });
                

            },

            select_day(date) {
                // api.post(`${API_URL}/webengdata`).then(res => {
                //     this.promotions = res.data.engdata.promotions;
                const day = parseInt(date.day.split('-').slice(2).join('-'))
                this.day_promotions.map((p,i) => {
                    if(parseInt(p.day) === day){
                        this.$set(this.day_promotions, i, {
                            day: p.day,
                            day_of_week: p.day_of_week,
                            pros: p.pros,
                            selected: true
                        })
                    }else{
                        this.$set(this.day_promotions, i, {
                            day: p.day,
                            day_of_week: p.day_of_week,
                            pros: p.pros,
                            selected: false
                        })
                    }
                })
            // })
            },
                
            show_popup (slug, items){
                if (slug === 'ALL') return false
                this.dialogVisible = true;
                this.url_img = slug;
                const detail = [];
                if (items){
                    items.map(t => {
                        this.endata.info_promotions.map(p => {
                            if (p.slug === t.slug){
                                detail.push(p)
                            }
                        })
                    })
                }
                if (detail.length > 2){
                    this.show_btn_next = true;
                    this.info_promotions = _.chunk(detail, 2)[0];
                    this.promotion_bk = detail;
                }else {
                    this.info_promotions = detail;
                    this.show_btn_next = false;
                }
            },

            next_page(index) {
                const info = this.promotion_bk;

                this.info_promotions = _.chunk(info, 2)[index]
                this.index_page = index + 1;
                if (this.index_page >= info.length / 2) this.show_btn_next = false
                this.show_btn_prev = true;

            },

            prev_page(index){
                const info = this.promotion_bk;
                if (index >= info.length / 2){
                    this.index_page = index - 2;
                }

                else this.index_page = index - 1;

                this.info_promotions = _.chunk(info, 2)[this.index_page]

                if (this.index_page <= 0) {
                    this.show_btn_prev = false;
                    this.index_page = 1
                }
                this.show_btn_next = true
            },

            daysInMonth(m){
                const m_31 = [1,3,5,7,8,10,12];
                if (m === 2) return 28
                if (m_31.includes(m)) return 31
                return 30
            },
            daysOfWeek(y, m , d) {
                const day = new Date(y,m,d).toDateString();
                return day.split(' ')[0].toUpperCase();
            }
        }
    }
</script>

